import React from "react";
import "./App.css";
import {
    BrowserRouter as Router, Route, Switch, useLocation
} from "react-router-dom";

import Footer from "./components/Footer";
import Nav from "./components/Nav";

import HomePage from "./pages/HomePage";
import ProjectPage from "./pages/ProjectPage";
import DesignPage from "./pages/DesignPage";
import ContactPage from "./pages/ContactPage";
import LearnPage from "./pages/LearnPage";
import "./index.css";

import ReactGA from "react-ga";
import RouteChangeTracker from "./RouteChangeTracker";

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            home: {
                title: "Home"
            },
            about: {
                title: "About"
            },
            contact: {
                title: "Contact"
            },
            project: {
                title: "Project"
            }
        };
    }

    render() {

        const TRACKING_ID = "UA-158612720-2"; // YOUR_OWN_TRACKING_ID
        ReactGA.initialize(TRACKING_ID);
        ReactGA.pageview(window.location.pathname);

        return (
            <Router>
                <RouteChangeTracker />
                <Nav />
                <Switch>
                    <Route path="/portfolio" exact component={HomePage} />
                    <Route path="/learning" component={LearnPage} />
                    <Route path="/design" component={DesignPage} />
                    <Route path="/contact" exact component={ContactPage} />
                    <Route path="/project/:page?" render={(props) => <ProjectPage title={this.state.project.title} {...props} />} />
                </Switch>
                {/* <Footer/> */}
            </Router>
        );
    }
} export default App;